import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { SchOneDayTimeViewModel } from "../../Model/ViewModel/SchOneDayTimeViewModel";

/**
 * Generated class for the SchOneDayTimeModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-sch-one-day-time-add-edit-modal",
  templateUrl: "sch-one-day-time-add-edit-modal.html"
})

export class SchOneDayTimeAddEditModalPage {
  item: SchOneDayTimeViewModel;
  mode: string;

  selectOptions:any;
  title:string;
  CanEditSch:boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
    this.item = new SchOneDayTimeViewModel();
    this.item.sch_no = navParams.data.item.sch_no;
    this.item.sch_name = navParams.data.item.sch_name;
    this.item.active_start_date = navParams.data.item.active_start_date;
    this.item.active_start_time = navParams.data.item.active_start_time;
    this.item.is_active = navParams.data.item.is_active;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.mode = navParams.data.mode;
    this.CanEditSch=navParams.data.CanEditSch;

   if(this.mode==="POST")
     this.title="新增";
   else
     this.title="更新";

  }

   Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad SchOneDayTimeModalPage");
  }
}



